var ClinicianChoose = React.createClass({
    getInitialState: function() {
        return {
            timeframe : null
        };
    },

    componentDidMount: function() {
        this.setTimeframe();
    },

    getDefaultProps: function() {
        return {
            clinicians : null,
            choose : null
        };
    },

    setTimeframe : function() {
        var timeframe;
        var date = new Date();
        var hours = date.getHours();
        switch (hours) {
            case hours < 12:
            timeframe = 'Morning';
            break;

            case hours > 17:
            timeframe = 'Evening';
            break;

            default:
            timeframe = 'Afternoon';
            break;
        }
        this.setState({
            timeframe : timeframe
        });
    },

    render: function() {
        var rows = null;

        if (this.props.clinicians !== null && this.props.clinicians.length > 0) {
            rows = this.props.clinicians.map(function(value, key){
                return <ClinicianRow key={key} {...value} choose={this.props.choose.bind(null, key)}/>;
            }.bind(this));
        } else {
            rows = <p>No clinicians found in system.</p>;
        }

        return (
            <div>
                <h2>Good {this.state.timeframe}!</h2>
                <hr />
                <h3>Please click/touch your name to continue...</h3>
                <div className="row clinician-container">
                    {rows}
                </div>
            </div>
        );
    }

});

var ClinicianRow = React.createClass({
    getInitialState: function() {
        return {
        };
    },

    getDefaultProps: function() {
        return {
            first_name : null,
            last_name : null,
            id : null,
            choose : null
        };
    },

    render: function() {
        return (
            <div className="col-sm-12 col-md-6">
                <button className="clinician btn btn-primary btn-lg btn-block" onClick={this.props.choose}>{this.props.first_name}&nbsp;{this.props.last_name}</button>
            </div>
        );
    }

});

var CompleteVisit = React.createClass({
    getInitialState: function() {
        return {
            dispositions : []
        };
    },

    getDefaultProps: function() {
        return {
            seen : null,
            clinician : null,
        };
    },

    componentDidMount: function() {
        this.loadData();
    },

    loadData : function() {
        $.getJSON('counseling/Admin/Clinician', {
        	command : 'dispositionList'
        }).done(function(data){
            this.setState({
                dispositions : data
            });
        }.bind(this));

    },

    handleClick : function(dispositionId) {
        $.post('counseling/Admin/Clinician', {
        	command : 'assignDisposition',
            dispositionId : dispositionId,
            visitId: this.props.seen.id
        }, null, 'json')
        	.done(function(data){
                this.props.setStage('selectVisitor');
        	}.bind(this));
    },

    render: function() {

        var dispositions = null;
        var buttonClass = null;
        var iconClass = null;

        dispositions = this.state.dispositions.map(function(value, key){
            buttonClass = 'btn btn-lg btn-block btn-' + value.color;
            iconClass = 'fa fa-' + value.icon;
            return (
                <button key={key} className={buttonClass} onClick={this.handleClick.bind(null, value.id)}><i className={iconClass}></i> {value.title}</button>
            );
        }.bind(this));

        return (
            <div>
                <h2>{this.props.clinician.first_name} {this.props.clinician.last_name} meeting with {this.props.seen.preferred_name} {this.props.seen.last_name}</h2>
                <h3>If your consultation is complete, choose a disposition below.</h3>
                {dispositions}
                <hr />
                <div className="text-center">
                    <button className="btn btn-default btn-lg" onClick={this.props.goBack}><i className="fa fa-undo"></i> Go Back</button>
                </div>
            </div>
        );
    }

});

var ClinicianTimeout = null;

var ClinicianDashboard = React.createClass({
    getInitialState: function() {
        return {
            clinicians : null,
            currentClinician : null,
            stage : 'choose',
            currentlySeen : null
        };
    },

    componentDidMount : function() {
        this.loadData();
    },

    loadData : function() {
        $.getJSON('counseling/Admin/Clinician', {
        	command : 'list'
        }).done(function(data){
            this.setState({
                clinicians : data
            });
        }.bind(this));
    },

    loadSeen : function(clinician) {
        $.getJSON('counseling/Admin/Clinician', {
        	command : 'currentlySeen',
            clinicianId : clinician.id
        }).done(function(data){
            if (data !== false) {
                this.setState({
                    currentlySeen : data,
                    stage : 'completeVisit',
                    currentClinician : clinician
                });
            } else {
                this.setState({
                    currentlySeen : null,
                    stage : 'selectVisitor',
                    currentClinician : clinician
                });
            }
        }.bind(this));

    },

    choose : function(key) {
        var clinician = this.state.clinicians[key];
        this.loadSeen(clinician);
    },

    setStage : function(stage) {
        this.setState({
            stage : stage
        });
    },

    componentDidUpdate: function(prevProps, prevState) {
        if (this.state.stage == 'reset') {
            ClinicianTimeout = setTimeout(function(){
                this.setStage('choose');
            }.bind(this), 5000);
        }
    },

    goBack : function() {
        clearTimeout(ClinicianTimeout);
        this.setStage('choose');
    },

    render: function() {
        switch (this.state.stage) {
            case 'choose':
            return <ClinicianChoose clinicians={this.state.clinicians} choose={this.choose}/>
            break;

            case 'selectVisitor':
            return <SelectVisitor clinician={this.state.currentClinician} setStage={this.setStage}/>
            break;

            case 'completeVisit':
            return <CompleteVisit clinician={this.state.currentClinician} seen={this.state.currentlySeen} goBack={this.goBack} setStage={this.setStage}/>
            break;

            case 'reset':
            return (
                <div className="well text-center">
                    <h2>Thank you.</h2>
                    <h3>Return on completion of your consultation.</h3>
                    <button className="btn btn-default btn-lg" onClick={this.goBack}><i className="fa fa-undo"></i> Go Back</button>
                </div>);
        }
    }

});

ReactDOM.render(<ClinicianDashboard/>, document.getElementById('clinician-dashboard'));

var SelectVisitor = React.createClass({
    getInitialState: function() {
        return {waiting: null, emergencies: null, selectedVisit: null,};
    },

    getDefaultProps: function() {
        return {clinician: null};
    },

    componentDidMount: function() {
        this.loadData();
    },

    loadData: function() {
        $.getJSON('counseling/Admin/Clinician', {command: 'visitorList'}).done(function(data) {
            if (data !== null) {
                this.setState({waiting: data.waiting, emergencies: data.emergencies,});
            }
        }.bind(this));
    },

    goBack: function() {
        this.props.setStage('choose');
    },

    reset: function() {
        this.setState({selectedVisit: null});
    },

    select: function(visit) {
        this.setState({selectedVisit: visit});
    },

    startConsultation() {
        var visitId = this.state.selectedVisit.id;
        var clinicianId = this.props.clinician.id;
        $.post('counseling/Admin/Clinician/', {
            command: 'selectVisit',
            visitId: visitId,
            clinicianId: clinicianId,
        }, null, 'json').done(function(data) {
            this.reset();
            this.props.setStage('reset');
        }.bind(this));
    },

    render: function() {
        if (this.state.selectedVisit !== null) {
            return <ConfirmVisitor
                visit={this.state.selectedVisit}
                goBack={this.reset}
                startConsultation={this.startConsultation}/>;
        }

        var listing = null;
        if (this.state.waiting === null && this.state.emergencies === null) {
            listing = (
                <div>
                    <div className="alert alert-info">No visitors are currently waiting.</div>
                    <div className="go-back text-center">
                        <button className="btn btn-default btn-lg" onClick={this.goBack}>
                            <i className="fa fa-undo"></i>
                            Go Back</button>
                    </div>
                </div>
            );
        } else {
            listing = <SelectVisitorListing
                waiting={this.state.waiting}
                emergencies={this.state.emergencies}
                setStage={this.props.setStage}
                clinician={this.props.clinician}
                reload={this.loadData}
                goBack={this.goBack}
                select={this.select}/>;
        }
        return (
            <div>
                <h2>Hello {this.props.clinician.first_name}</h2>
                <hr/> {listing}
            </div>
        );
    },
});

var ConfirmVisitor = React.createClass({
    getDefaultProps: function() {
        return {visit: null};
    },

    render: function() {
        var visitor = this.props.visit.visitor;
        return (
            <div className="text-center well">
                <h2>You have chosen to start a consulation with {visitor.preferred_name}
                    {visitor.last_name}</h2>
                <div className="go-back">
                    <button
                        className="btn btn-success btn-lg"
                        style={{
                        marginBottom: '1em'
                    }}
                        onClick={this.props.startConsultation}>
                        <i className="fa fa-check"></i>
                        Start consultation</button><br/>
                    <button className="btn btn-default btn-3x" onClick={this.props.goBack}>
                        <i className="fa fa-undo"></i>
                        Start over</button>
                </div>
            </div>
        );
    },
});

var SelectVisitorListing = React.createClass({
    getDefaultProps: function() {
        return {waiting: null, emergencies: null,};
    },

    render: function() {
        return (
            <div className='visitor-listing'>
                <Emergencies
                    list={this.props.emergencies}
                    clinician={this.props.clinician}
                    select={this.props.select}/>
                <Waiting
                    waiting={this.props.waiting}
                    clinician={this.props.clinician}
                    select={this.props.select}/>
                <div className="go-back text-center">
                    <button className="btn btn-default btn-lg" onClick={this.props.goBack}>
                        <i className="fa fa-undo"></i>
                        Go Back</button>
                </div>
            </div>
        );
    },
});

var Emergencies = React.createClass({
    getDefaultProps: function() {
        return {list: null, clinician: null, select: null,};
    },

    render: function() {
        if (this.props.list === null || this.props.list.length === 0) {
            return null;
        } else {
            var visits = this.props.list.map(function(value, key) {
                return (<VisitorRow
                    key={value.id}
                    {...value}
                    clinician={this.props.clinician}
                    buttonClass="danger"
                    select={this.props.select.bind(null, value)}/>);
            }.bind(this));

            return (
                <div>
                    <h3>Emergencies</h3>
                    {visits}
                </div>
            );
        }
    },
});

var Waiting = React.createClass({
    getDefaultProps: function() {
        return {waiting: null, clinician: null,};
    },

    render: function() {
        let visits = <div>No walk-ins waiting</div>;
        if (this.props.waiting !== null && this.props.waiting.length !== 0) {
            visits = this.props.waiting.map(function(value, key) {
                return (<VisitorRow
                    key={value.id}
                    {...value}
                    clinician={this.props.clinician}
                    buttonClass={value.color}
                    select={this.props.select.bind(null, value)}/>);
            }.bind(this));
        }

        return (
            <div>
                <h3>Waiting</h3>
                {visits}
            </div>
        );

    },
});

var VisitorRow = React.createClass({
    getDefaultProps: function() {
        return {visitor: null, buttonClass: 'success', clinician: null, select: null,};
    },

    render: function() {
        let _className = 'btn btn-block btn-lg btn-' + this.props.buttonClass;
        let waiting = null;
        if (this.props.category == '1') {
            waiting = <span>&nbsp;- Waiting: {this.props.wait_time} min.</span>;
        }
        return (
            <button className={_className} onClick={this.props.select}>
                <CategoryIcon category={this.props.category} title={this.props.reason_title}/>
                &nbsp;<strong>{this.props.visitor.preferred_name} {this.props.visitor.last_name}</strong>
                {waiting}
            </button>
        );
    },
});

var CategoryIcon = React.createClass({
    getDefaultProps: function() {
        return {category: 0, title: null,};
    },

    render: function() {
        var icon = null;
        var _className = 'category fa fa-lg ' + categoryIcons[this.props.category];
        icon = <i className={_className} title={this.props.title}></i>;
        return (
            <span>{icon}</span>
        );
    },
});
